:root {
  --white-color: #fff;
  --red-color: #ae1332;
  --black-color: #000;
  --background-green-color: #a4cb66;
  --background-yellow-color: #f5ce13;
  --background-light-green-color: #a1c864;
  --strawberry-color: #f8766b;
  --banana-color: #edd251;
  --watermelon-color: #9dc262;
  --lime-color: #bfd663;
  --apple-color: #e93f29;
  --peach-color: #db4f4b;
  --plum-color: #9b5366;
  --orange-color: #f47f4c;
  --kiwi-color: #98b37e;
  --pop-up-color: #e03f5e;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
}

img {
  display: block;
}

.list {
  list-style: none;
}

.link {
  color: inherit;
  text-decoration: none;
}

img {
  width: 100%;
  max-width: 100%;
  height: auto;
  display: block;
}

body {
  letter-spacing: -.56px;
  margin: 0;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
}

.container {
  max-width: 375px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}

.header {
  background: #fff;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

.header-container {
  color: var(--red-color);
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  display: flex;
  position: relative;
}

.header-navigation {
  display: none;
}

@media screen and (min-width: 768px) {
  .header-navigation {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .header-list {
    align-items: center;
    gap: 16px;
    display: inline-flex;
  }
}

@media screen and (min-width: 1280px) {
  .header-list {
    align-items: center;
    gap: 20px;
    display: inline-flex;
  }
}

.hamburger-link:hover, .header-link:focus {
  color: var(--watermelon-color);
}

.link-underline {
  display: inline-block;
  position: relative;
}

.link-underline:after {
  content: "";
  width: 100%;
  height: 4px;
  background-color: var(--watermelon-color);
  border-radius: 10px;
  transition: transform .25s ease-out;
  display: block;
  position: absolute;
  bottom: -3px;
  transform: scaleX(0);
}

.link-underline:hover:after {
  transform-origin: 0 100%;
  transform: scaleX(1);
}

.header-icon {
  fill: currentColor;
  align-items: center;
  gap: 8px;
  display: inline-flex;
}

@media screen and (min-width: 768px) {
  .header-icon {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
  }
}

@media screen and (min-width: 1280px) {
  .header-icon {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
  }
}

.header-logo {
  padding: 16px 0;
  display: block;
}

.logo-icon {
  align-items: center;
}

.shopping-cart {
  fill: currentColor;
  visibility: hidden;
}

@media screen and (min-width: 768px) {
  .shopping-cart {
    visibility: visible;
  }
}

@media screen and (min-width: 1280px) {
  .shopping-cart {
    visibility: visible;
  }
}

.menu-button {
  visibility: visible;
  color: var(--red-color);
  border: none;
  display: flex;
}

@media screen and (min-width: 768px) {
  .menu-button {
    display: none;
  }
}

@media screen and (min-width: 1280px) {
  .menu-button {
    display: none;
  }
}

.menu-toggle {
  fill: currentColor;
  stroke: var(--red-color);
  cursor: pointer;
  background: none;
  border: rgba(0, 0, 0, 0);
}

.menu-icon {
  color: var(--red-color);
}

.menu-container {
  width: 100vw;
  height: 100%;
  background-color: var(--white-color);
  flex-direction: column;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
