@keyframes fadeInDownMy {
  0% {
    opacity: 0;
    -webkit-transform: translateY(calc(var(--anim-offset) * -1) );
    transform: translateY(calc(var(--anim-offset) * -1) );
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInLeftMy {
  0% {
    opacity: 0;
    -webkit-transform: translateX(calc(var(--anim-offset) * -1) );
    transform: translate(calc(var(--anim-offset) * -1) );
  }

  to {
    opacity: 1;
    -webkit-transform: translateX(0);
    transform: translate(0);
  }
}

@keyframes fadeInRightMy {
  0% {
    opacity: 0;
    -webkit-transform: translateX(var(--anim-offset) );
    transform: translate(var(--anim-offset) );
  }

  to {
    opacity: 1;
    -webkit-transform: translateX(0);
    transform: translate(0);
  }
}

